/**
 * @file コンテンツスクリプト
 * Chrome拡張はコンテンツ、バッググラウンド、フレーム(iframe)の3つのコンテキストで実行されます。
 * DOMへのアクセスはウェブページに挿入されるコンテンツスクリプトが担います。
 */
import { EXT_ORIGIN, createMessageAgent } from "@nyanator/chrome-ext-utils";
import { KindMessageData } from "utils/message-def";

import "./preview/html-preview.js";
import { ChatGPTWebDeveloperContentScript } from "./script-class/chatgpt-webdeveloper-content-script";
import * as ChatGPTUtils from "./utils/chat-gpt-utils";

// IIFE
(async () => {
  // コンテキスト間でのメッセージ通信を準備
  const messageAgent = await createMessageAgent<KindMessageData>({
    runtimeId: chrome.runtime.id,
    allowedOrigins: [ChatGPTUtils.ORIGIN, EXT_ORIGIN],
  });

  // コンテンツスクリプトを準備
  const chatGPTWebDeveloperContentScript = new ChatGPTWebDeveloperContentScript(
    messageAgent,
  );
  await chatGPTWebDeveloperContentScript.initialize();
})();
